@import url(https://fonts.googleapis.com/css?family=Oxygen);
body {
  margin: 0;
  font-family: Oxygen;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  color: white;
  width: 100%;
}

html {
  scroll-behavior: smooth;
}

a:link {
    text-decoration: none;
}

.logo-home {
    height: 60px;
}

.nav-bar{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;
    min-height: 10vh;
    background: transparent;
    width: 100%;
    position: fixed;

    transition-timing-function: ease-in;
    transition: 0.5s;
}

.nav-bar.active {
    min-height: 6vh;
    background-color: white;
}

nav.nav-bar li {
    font-size: 20px;
    color: white;
}

nav.nav-bar.active li {
    font-size: 20px;
    color: black;
}

nav.nav-bar li:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    text-decoration: underline;
}

.nav-links{
    width: 30%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;
    list-style: none;
}

@media only screen and (max-width: 600px) {
    .nav-links{
        width: 75%;
        display: -webkit-flex;
        display: flex;
        -webkit-justify-content: space-around;
                justify-content: space-around;
        -webkit-align-items: center;
                align-items: center;
        list-style: none;
    }
}
.page-1{
    text-align: center;
    height: 100vh;
    background-image: url(/project_w/static/media/welcome.2500c290.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 0% 50%;
}
  
#startHeader {
    padding-top: 25vh;
    font-weight: bold;
    font-size: 65px;
    color: white;
}

#startSummary {
    font-size: 40px;
}
#page-2{
    background-color: #191919;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

.about-me-header {
    max-height: 50px;
    font-size: 40px;
    padding: 25px 0px 50px 0px;
}

.container-upper {
    max-width: 1000px;
    width: 75%;
    padding: 25px 15px 50px 15px;
}

.container-lower {
    max-width: 1000px;
    width: 75%;
    padding: 25px 15px 75px 15px;
}

.text-container {
    font-size: 20px;
}
#page-3{
    text-align: center;
    height: 45vh;
    background-color: white;
}

.project-header {
    font-size: 40px;
    color: black;
    padding: 15px;
}

.centered {
    margin: auto;
    width: 85vw;
}
  
.horizontal-menu {
    margin: auto;
    width: 100%;
    padding: 10px;
}

.project-item-container {
    background-color: lightcyan;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    width: 25vh;
    height: 25vh;
    margin-left: 25px; margin-right: 25px;
    border-radius: 10px;
    border: solid 1px #eaeaea;
    box-shadow: 0 2px 6px 0 rgb(0 0 0 / 15%);
}

.project-item-content {
    position: absolute;
    bottom: 0;
    width: inherit;
    height: 70%;

    color: black;
    text-align: center;
    font-size: 2vh;
}

.project-language {
    position: absolute;
    width: inherit;
    bottom: 0;
    height: 50%;

    color: black;
    text-align: center;
    font-size: 1.5vh;
}

.project-github-logo {
    position: absolute;
    bottom: 0;
    width: 10vh;
}

.menu-item {
    color: black;
    padding: 0 8px;
    margin: 5px 10px;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer;
    border: none;
}

.arrow-prev, .arrow-next {
    font-size: 7.5vh;
    color: black;
    padding: 20px;
    cursor: pointer;
}
.cv {
    height: 100vh;
    display: -webkit-flex;
    display: flex;
}

.image {
    position: absolute;
    top: 8vh;
    float: left;
    border-radius: 50%;
    width: 10vw;
}

.header-text {
    position: absolute;
    top: 10vh;
    left: 12vw;
    color: black;
    font-size: 45px;
    text-decoration: underline;
}

.introduction {
    color: black;
    position: absolute;
    top: 20vh;
    left: 12vw;
    width: 75%;
    padding-left: 300px;
    font-size: 25px;
}

.profile-header {
    color: black;
    position: absolute;
    top: 30vh;
    width: 75%;
    left: 12vw;
    font-size: 40px;
}

.profile-header li {
    font-size: 25px;
    padding-top: 25px;
    margin-left: 50px;
}
#footer {
    text-align: center;
    background-color: #191919;
    min-height: 20vh;
    -webkit-justify-content: center;
            justify-content: center;
}

.contact-header {
    font-size: 30px;
    padding-top: 40px;
}

.email {
    font-size: 20px;
    padding-top: 20px;
}

.github {
    padding-top: 30px;
    margin-right: 25px;
    color: white;
}

.linkedin {
    padding-top: 30px;
    margin-left: 25px;
    color: white;
}
